.signUp {
  display: grid;
  justify-items: center;
}

.form-container {
  margin-top: 5%;
  margin-bottom: 10%;
}
@media (min-width: 998px) {
  .form-container {
    margin-top: 5%;
    margin-bottom: 10%;
  }
}

.form {
  background-color: #b3b0b06c;
  padding: 30px;
  border: 1px solid rgb(129, 121, 121);
  border-radius: 1px;
}
.signUpLogo {
  width: 200px;
  object-fit: contain;
  padding: 10px 0px;
}

.loginPara {
  color: rgba(var(--f52, 142, 142, 142), 1);
  font-size: 17px;
  font-weight: 600;
  line-height: 20px;
  margin: 0px 40px 20px 40px;
}

select,
input {
  padding: 5px;
  margin: 3px 0px;
  outline: none;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

.form > div {
  width: 80%;
  margin: auto;
}

#submit-btn {
  background-color: #1773ea;
  border: none;
  border-radius: 3px;
  color: white;
  font-weight: bold;
  padding: 5px 10px;
  width: 80%;
  cursor: pointer;
  margin-top: 8px;
}

.form2 {
  background-color: #b3b0b06c;
  padding: 30px;
  margin-top: 5px;
  border: 1px solid rgb(129, 121, 121);
  border-radius: 1px;
}
