.card-0 {

    padding: 100px 0px 50px 0px;

    color: white;
    border: 0px;
  }
  
  
  .container {
   padding: 20px 0px 20px 0px;
    border-radius: 20px;
  }
  
  .btn {
    letter-spacing: 1px;
  }
  
  select:active {
    box-shadow: none !important;
    outline-width: 0 !important;
  }
  select:after {
    box-shadow: none !important;
    outline-width: 0 !important;
  }
  input,
  textarea {
    padding: 10px 12px 10px 12px;
    border: 1px solid lightgrey;
    border-radius: 0px !important;
    margin-bottom: 5px;
    margin-top: 2px;
    width: 100%;
    box-sizing: border-box;
    color: #2c3e50;
    font-size: 14px;
    letter-spacing: 1px;
    resize: none;
  }
  
  select:focus,
  input:focus {
    box-shadow: none !important;
    border: 1px solid #2196f3 !important;
    outline-width: 0 !important;
    font-weight: 400;
  }
  
  label {
    margin-bottom: 2px;
    font-weight: bolder;
    font-size: 14px;
  }
  
  input:focus,
  textarea:focus {
    -moz-box-shadow: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border: 1px solid #0626db;
    outline-width: 0;
  }
  
  button:focus {
    -moz-box-shadow: none !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    outline-width: 0;
  }
  
  .form-control {
    height: calc(2em + 0.75rem + 3px);
  }
  
  .inner-card {
    margin: 79px 0px 70px 0px;
  }

  
  .card-1 {
    border-radius: 17px;
    color: black;
    box-shadow: 2px 4px 15px 0px rgb(0, 0, 0, 0.5) !important;
  }
  
  #file {
    border: 2px dashed #92b0b3 !important;
  }
  
  .color input {
    background-color: #f1f1f1;
  }
  
  .files:before {
    position: absolute;
    bottom: 60px;
    left: 0;
    width: 100%;
    content: attr(data-before);
    color: #000;
    font-size: 12px;
    font-weight: 600;
    text-align: center;
  }
  
  #file {
    display: inline-block;
    width: 100%;
    padding: 95px 0 0 100%;
    background: url("https://i.imgur.com/VXWKoBD.png") top center no-repeat #fff;
    background-size: 55px 55px;
  }
  