@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");

.carousel-item {
  width: 100%;
}
.price span {
  font-size: 18px;
}
.cut {
  text-decoration: line-through;
  color: red;
}
.icons i {
  font-size: 17px;
  color: green;
  margin-right: 2px;
}
.offers i {
  color: green;
}
.delivery i {
  color: blue;
}
label.radio {
  cursor: pointer;
}
label.radio input {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
  pointer-events: none;
}
label.radio span {
  padding: 2px 11px;
  margin-right: 3px;
  border: 1px solid #8f37aa;
  display: inline-block;
  color: #8f37aa;
  border-radius: 3px;
  text-transform: uppercase;
}
label.radio input:checked + span {
  border-color: #8f37aa;
  background-color: #8f37aa;
  color: #fff;
}
